@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,400&display=swap");
* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}
.twitter {
  display: flex;
}
/* Start Sidebar Here */
.logo {
  font-size: 60px;
  color: #1d9bf0;
  padding-left: 15px;
  padding-right: 15px;
  display: inline-block;
  width: 60px;
  height: 60px;
  cursor: pointer;
  border-radius: 50%;
  background: none;
  transition: background 0.5s;
}
.logo:hover {
  background-color: #c2def16e;
}
.sidebar {
  position: fixed;
  left: 0;
  width: 25%;
  height: 100vh;
  border-right: 1px solid #eff3f4;
}

.sidebar ul {
  margin: 5px 20%;
}
.sidebar ul li a {
  display: flex;
  align-items: center;
  font-size: 23px;
  color: #0f1419;
  padding: 13px;
}
.sidebar ul li a .icon {
  margin-right: 10px;
}
.sidebar ul li a:hover {
  background: #eff3f4;
  border-radius: 15px;
}
.sidebar ul li .more {
  width: 25px;
  height: 25px;
  border: 2px solid black;
  border-radius: 50%;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sidebar ul li .more span {
  display: inline-block;
  width: 3px;
  height: 3px;
  background: #000;
  border-radius: 50%;
  text-align: center;
  margin-right: 1px;
}
.sidebar ul li button {
  background: #1d9bf0;
  padding: 10px 0;
  width: 200px;
  color: white;
  font-size: 23px;
  border: none;
  outline: none;
  border-radius: 30px;
  margin-top: 10px;
  transition: background 0.5s;
  cursor: pointer;
}
.sidebar ul li button:hover {
  background: #0c78c0;
}
.sidebar ul li .twitter_id {
  width: 100%;
  margin: 0;
  margin-top: 10px;
}
.sidebar ul li .twitter_id img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid bisque;
  margin-right: 5px;
}
.sidebar ul li .twitter_id h4 {
  font-size: 16px;
  font-weight: bold;
}
.sidebar ul li .twitter_id p {
  font-size: 14px;
  font-weight: normal;
  color: #aab2b9;
}

/* Start Tweet section */
/* *{
  border: 1px solid red;
} */
.tweetbar{
  position: fixed;
  left: 25%;
  border-right: 1px solid #eff3f4;
  width: 50%;
  height: 100vh;
  overflow-y: scroll;
  -ms-overflow-style: none;  
  scrollbar-width: none; 
}
.tweetbar::-webkit-scrollbar {
  display: none;
}
.tweetbar .heading{
  display: flex;
  padding: 10px 20px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eff3f4;
}
#tweet_box{
  position: relative;
}
#tweet_box>div{
  padding: 10px 20px;
  padding-bottom: 0;
  display: flex;
  justify-content: space-between;
}
#tweet_box img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
#tweet_box textarea{
  width: 100%;
  height: 80px;
  padding: 10px;
  margin-bottom: 0;
  border: none;
  outline: none;

  text-indent: 10px;
  font-size: 18px;
  transition: height .5s;
  resize: none;
}
#tweet_box textarea:focus{
  height: 130px;
  border-bottom: 1px solid #eff3f4;
}
#tweet_box  #input_text{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 70px;
  left: 70px;
  color:#1d9bf0;
  text-align: center;
  padding: 5px 10px;
  background: none;
  border-radius: 20px;
  transition: background .5s;
  cursor: pointer;
}
#tweet_box  #input_text:hover{
  background: #1d9cf018;
}
#tweet_box  #input_text p{
  margin-left: 5px;
}

#tweet_box .tweet_bottom{
  padding: 10px 20px;
  border-bottom: 1px solid #eff3f4;
  display: flex;
  align-items: center;
}
#tweet_box .tweet_bottom div{
  margin-left: 50px;
}
#tweet_box .tweet_bottom .icon span{
  display: inline-block;
  font-size: 20px;
  margin-right: 20px;
  color:#1d9bf0;
  cursor: pointer;
  background: none;
  padding: 10px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
#tweet_box .tweet_bottom .icon span:hover{
  background-color: #c2def16e;
}
#tweet_box .tweet_bottom .icon .imstartbar{
  transform: rotate(90deg);
}
#tweet_box #tweet_btn{
  padding-left: 20px;
  border-left: 1px solid #eff3f4;
  display: flex;
  align-items: center;
  justify-content: center;
}
#tweet_box #tweet_btn .tweet_plus{
  width: 20px;
  height: 20px;
  padding: 2px;
  color:#1d9bf0;
  border: 1px solid gray;
  border-radius: 50%;
  text-align: center;
  font-size: 10px;
  cursor: pointer;
  display: inline-block;
}
#tweet_box #tweet_btn button{
  background:#1d9bf0;
  color: white;
  padding: 10px 25px;
  border: none;
  outline: none;
  border-radius: 20px;
  cursor: pointer;
  margin-left: 10px;
}
/* tweet feed */
#tweet_feed{
  padding: 20px;
}
.tweet_card{
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #eff3f4;
}
.tweet_card>img{
  margin-right: 10px;
  margin-top: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid black;
}
.tweet_card_body .tweet_id{
  display: flex;
  align-items: center;
}
.tweet_card_body .tweet_id span{
  display: inline-block;
  margin-left: 5px;
}
.tweet_card_body .tweet_id span:first-child{
  font-weight: bold;
}
.tweet_card_body .tweet_id span:last-child{
  color: #7b8892;
}
.tweet_card_body .tweet_content p{
  margin: 10px auto;
}
.tweet_card_body .tweet_content img{
  width: 100%;
}
.tweet_card_body .tweet_content .tweet_body_icon{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  font-size: 20px;
}
.tweet_card_body .tweet_content .tweet_body_icon div{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #536471;
  font-size: 20px;
}
.tweet_card_body .tweet_content .tweet_body_icon div .icon{
  margin-right: 10px;
  display: inline-block;
}
/* trendbar */
.trendbar{
  position: fixed;
  right: 0;
  width: 25%;
  padding:0px 20px;
}
.trendbar .search{
  width: 100%;
  display: flex;
  align-items: center;
  margin:5px auto 10px auto;
}
 
.trendbar .search span{
  display: inline-block;
  position: absolute;
  left: 30px;
  color: #5c6c79;
  background: #eff3f4;
  
}
.trendbar .search input{
  display: inline-block;
  width: 100%;
  padding: 10px;
  background: none;
  outline: none;
  border: 1px solid #eff3f4;
  text-indent: 30px;
  color: #5c6c79;
  background: #eff3f4;
  font-size: 18px;
  border-radius: 20px;
}
 
.trendbar .search input:focus{
  border:#0c78c0 1px solid;
}
.trendbar .trend_feed{
  background: #eff3f4;
  border-radius: 20px;
}
.trendbar .trend_feed .card{
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 20px auto;
}
.trendbar .trend_feed .card:hover{
  background: #cfd2d39b;
}
.trendbar .trend_feed .card img{
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin: 4px;
}
.trendbar .footer{
  padding-left: 5px;
}
.trendbar .footer span{
   display: inline-block;
   margin-right: 15px;
   margin-bottom: 5px;
   color: #5f5f5f;
   font-size: 13px;
}
.trendbar .footer span:hover{
   text-decoration: underline;
}
.trendbar .footer span:last-child:hover{
   text-decoration: none;
}